import {type FC } from "react";
import { useTranslation } from "react-i18next";
import HexImage from "@/components/ui/HexImage";
const internship_image = "";
const InternshipImage: FC = () => {
    const { t } = useTranslation();
    return <HexImage
        hexData={internship_image}
        imageType="jpg"
        alt={t(`homepage.services.internships.title`)}
        aspectRatio="square"
        className="w-full"
    />
}
export default InternshipImage;